import config from './config';
import templates from './ui-template';
// import helpers from './helpers';
import menu from './menu';
import _dashboard from './modules/_dashboard';

var ui = {};

var $ = jQuery;

ui.initUI = function(anchordiv) {
  anchordiv = typeof anchordiv !== 'undefined' ? anchordiv : 'app';
	if (!window.indexedDB) {
    window.alert("Your browser doesn't support a stable version of IndexedDB. Such and such feature will not be available.");
  } else {
    if (window.Worker) {
      document.title = config.documentTitle;
      ui.createTopNavigation(anchordiv);
      menu._addModuleMenu();
      ui.createModalBoxes(anchordiv);
      menu._activateTopNav();
      ui.createLandingPage();
      ui._activateWorker();
    } else {
      window.alert("Your browser doesn\'t support web workers.");
    }
  }
};

ui.createTopNavigation = function(anchordiv) {
  anchordiv = typeof anchordiv !== 'undefined' ? anchordiv : 'app';
  let anchorSelector = document.getElementById('app');
  let activeModule = sessionStorage.getItem('module');
  if(activeModule){
    anchorSelector.insertAdjacentHTML('beforebegin', templates.topAppMainNavigation());
  } else {
    anchorSelector.insertAdjacentHTML('beforebegin', templates.topLandingPageNavigation());
  }
};

ui.createModalBoxes = function(anchordiv) {
  anchordiv = typeof anchordiv !== 'undefined' ? anchordiv : 'app';
  let anchorSelector = document.getElementById('app');
  anchorSelector.insertAdjacentHTML('afterend', templates.modalBoxes());
};

ui.createLandingPage = function() {
  let activeModule = sessionStorage.getItem('module');
  if(activeModule){
    _dashboard._createDashboardLayout();
  } else {
    let anchorSelector = document.getElementById('app');
    anchorSelector.innerHTML = templates._frontpageCarousel();
  }
};

/*
 * Important: worker init
 * */
ui._activateWorker = function() {
  var mainWorker = new Worker('worker.js');
  mainWorker.addEventListener('message', function(e) {
    console.log('Main Worker said: ', e.data);
  }, false);
  // mainWorker.postMessage('Main Worker activated!');
}

export default ui;