import templates from './ui-template';
import helpers from './helpers';
import dashboard from './modules/_dashboard';
import menudata from './modules/_data';
import report from './modules/_report';
import menumap from './modules/_map';
import menusetting from './modules/_setting';
import administrator from './modules/_administrator';

var menu = {};

var $ = jQuery;

menu._activateTopNav = function() {
  $('#navbarSupportedContent > ul.navbar-nav > li.nav-item > a.nav-link').on('click', function(evt){
    evt.stopImmediatePropagation();
    $('#navbarSupportedContent.navbar-collapse.collapse.show').removeClass().addClass('navbar-collapse collapse');
    let itemid = $(this).attr('id');
    switch(itemid) {
      case 'home':
          document.getElementById('app').innerHTML = "";
          document.getElementById('app').innerHTML = templates._frontpageCarousel();
        break;
      case 'dashboard':
          document.getElementById('app').innerHTML = "";
          dashboard._createDashboardLayout();
        break;
      case 'data':
          document.getElementById('app').innerHTML = "";
          menudata._createDataLayout();
        break;
      case 'report':
          document.getElementById('app').innerHTML = "";
          report._createReportLayout();
        break;
      case 'map':
          document.getElementById('app').innerHTML = "";
          menumap._createMapLayout();
        break;
      case 'setting':
          document.getElementById('app').innerHTML = "";
          menusetting._createSettingLayout();
        break;
      case 'admintools':
          document.getElementById('app').innerHTML = "";
          administrator._createAdministratorLayout();
        break;
      case 'login':
          helpers._doLoginBox();
        break;
      case 'exit':
          helpers._signout();
        break;
      default:
          console.log('undefined');
        break;
    }
    return false;
  });
}

menu._addModuleMenu = function() {
  let activeModule = sessionStorage.getItem('module');
  let leftMenu = document.getElementById('topnavleftmenu').firstElementChild;
  let rightMenu = document.getElementById('topnavrightmenu').firstElementChild;
  if(activeModule){
    leftMenu.insertAdjacentHTML('afterend', templates._domLeftTopNavigation());
    rightMenu.insertAdjacentHTML('beforebegin', templates._domRightTopNavigation());
  } else {
    return false;
  }
}

export default menu;