import config from '../config';
import helpers from '../helpers';
import _templates from './_component-templates';
import _helpers from './_layout-helpers';
import {DataTable} from '../../node_modules/simple-datatables/dist/index';

var administrator = {};

administrator._createAdministratorLayout = function() {
  document.getElementById('app').innerHTML = _templates._baseSidebarLayoutTemplate();
  document.getElementById('sidebar').innerHTML = _templates._administratorSidebarMenu();
  this._activateSidebarMenu();
};

administrator._activateSidebarMenu = function() {
  let elements = document.querySelectorAll('.list-group-item');
  for (let i = 0; i < elements.length; i++) {
    let self = elements[i];
    self.addEventListener('click', function (event) {  
      event.preventDefault();
      if (document.querySelector('.list-group-item.list-group-item-action.active') !== null) {
        document.querySelector('.list-group-item.list-group-item-action.active').classList.remove('active');
      }
      switch(this.id){
        case 'orgbidang':
            administrator._createModuleBidangManagement();
          break;
        case 'orgsubbidang':
            administrator._createModuleSubBidangManagement();
          break;
        case 'usersmanagement':
            administrator._createModuleUsersManagement();
          break;
        case 'notarisppatusers':
            administrator._createModuleNotarisPPATUsers();
          break;
        default:
          break;
      }
      this.classList.toggle('active');
    }, false);
  }
};

administrator._createModuleBidangManagement = function() {
  let dom = _templates._bidangManagement();
  document.getElementById('app_content').innerHTML = dom;
  this._populateBidangDatatable();
};

administrator._createModuleSubBidangManagement = function() {
  let dom = _templates._subbidangManagement();
  document.getElementById('app_content').innerHTML = dom;
  this._populateSubBidangDatatable();
};

administrator._createModuleUsersManagement = function() {
  let dom = _templates._usersManagement();
  document.getElementById('app_content').innerHTML = dom;
  this._populateUsersDatatable();
  this._activateBtnAddUser();
};

administrator._createModuleNotarisPPATUsers = function() {
  let dom = _templates._notarisPPATUsers();
  document.getElementById('app_content').innerHTML = dom;
  this._populateNotarisPPATDatatable();
};

administrator._activateBtnAddUser = function() {
  let activeModule = sessionStorage.getItem('module');
  if(activeModule == 'development'){
    document.getElementById('btnresetuser').addEventListener('click', function(evt){
      let confirmresetusers = confirm('Benar-benar akan me-reset seluruh user?');
      if(confirmresetusers == true){
        fetch(''+config.endPointBaseURL+'/dataservices/sysResetUsers', {
          method: 'POST',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({"command":"reset-all-users"})
        })
        .then(function(response){
          return response.json();
        })
        .then(function(data){
          if (data.status == 201) {
            administrator._createModuleUsersManagement();
          } else {
            document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Data users gagal di-reset.</div>";
            setTimeout(function(){
              document.getElementById('appFormNotice').innerHTML = '';
              administrator._createModuleUsersManagement();
            },2000);
          }
        })
        .catch(function(err){
          document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Error: "+ err +".</div>";
          setTimeout(function(){
            document.getElementById('appFormNotice').innerHTML = '';
          },2000);
        });
      } else {
        return false;
      }
    }, true);
  }
  document.getElementById('btnadduser').addEventListener('click', function(evt){
    document.getElementById('app_body').innerHTML = _templates._createFormAddNewUser();
    administrator._activateAddUserForm();
    document.getElementById('realname').focus();
  }, true);
};

administrator._activateAddUserForm = function() {
  administrator._populateBidangSelector();
  document.getElementById('is_verificator').addEventListener('change', function(evt){
    if(evt.target.checked){
      document.getElementById('verificator').value = '1';
    } else {
      document.getElementById('verificator').value = '0';
    }
  });
  document.getElementById('is_collector').addEventListener('change', function(evt){
    if(evt.target.checked){
      document.getElementById('collector').value = '1';
    } else {
      document.getElementById('collector').value = '0';
    }
  });
  document.getElementById('subbidang').disabled = true;
  document.getElementById('bidang').addEventListener('change', function(evt){
    let bidangvalue = this.value;
    let bidangarray = bidangvalue.split('|');
    document.getElementById('subbidang').disabled = false;
    document.getElementById('subbidang').innerHTML = "";
    administrator._populateSubBidangSelector(bidangarray[0]);
  });
  document.getElementById('cancelform').addEventListener('click', function(){
    administrator._createModuleUsersManagement();
  });
  document.getElementById('submitform').addEventListener('click', function(evt){
    evt.preventDefault();
    administrator._addNewUser();
    return false;
  });
};

