administrator._addNewUser = function() {
  let irn = document.getElementById('realname').value, 
    iun = document.getElementById('username').value, 
    ibd = document.getElementById('bidang').value, 
    isb = document.getElementById('subbidang').value, 
    iin = document.getElementById('inisial').value;
  if(irn.length < 4 || iun.length < 4 || iin.length < 3 || ibd == '0|_UNDEFINED_|_UNDEFINED_'){
    document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Data yang Anda input tidak memenuhi syarat. Periksa kembali input Anda.</div>";
    setTimeout(function(){
      document.getElementById('appFormNotice').innerHTML = '';
    },2000);
  } else {
    var form = document.querySelector('#appForm'),
      formData = helpers.serialize(form), 
      jsonData = helpers.QueryStringToJSON(decodeURIComponent(formData));
    fetch(''+config.endPointBaseURL+'/dataservices/createNewUser', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(jsonData)
    })
    .then(function(response){
      return response.json();
    })
    .then(function(data){
      if (data.status == 201) {
        console.log(data);
        administrator._createModuleUsersManagement();
      } else if(data.status == 203){
        document.getElementById('usernameHelp').textContent = "Username ini sudah dipakai. Pilih username yang lain.";
        document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Data gagal disimpan. Periksa kembali isian data Anda.</div>";
        setTimeout(function(){
          document.getElementById('usernameHelp').textContent = "* Harus diisi, minimal 4 (empat) karakter [A-Z][a-z][0-9].";
          document.getElementById('appFormNotice').innerHTML = '';
        },2000);
      } else if(data.status == 204){
        document.getElementById('initialHelp').textContent = "Inisial ini sudah dipakai. Pilih inisial yang lain.";
        document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Data gagal disimpan. Periksa kembali isian data Anda.</div>";
        setTimeout(function(){
          document.getElementById('initialHelp').textContent = "* Harus diisi, minimal 3 (tiga) karakter, maksimal 8 (delapan) karakter [A-Z][0-9].";
          document.getElementById('appFormNotice').innerHTML = '';
        },2000);
      } else {
        document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Data gagal disimpan.</div>";
        setTimeout(function(){
          document.getElementById('appFormNotice').innerHTML = '';
        },2000);
      }
    })
    .catch(function(err){
      document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Error: "+ err +".</div>";
      setTimeout(function(){
        document.getElementById('appFormNotice').innerHTML = '';
      },2000);
    });
  }
};

