administrator._populateBidangSelector = function(){
  fetch(''+config.endPointBaseURL+'/dataservices/listObjects?listall=bidang')
  .then(function(response){
    return response.json();
  })
  .then(function(data){
    if (data.status == 201) {
      const bidangSelector = document.getElementById('bidang');
      let datarow = data.dataarray;
      datarow.forEach(function(value){
        let option = document.createElement('option');
        option.text = value.bidang;
        option.value = value.id+'|'+value.bidang+'|'+value.module;
        bidangSelector.appendChild(option);
      });
    } else {
      
    }
  })
  .catch(function(err){

  });
};

administrator._populateBidangSelectorWithSelected = function(selectedid){
  fetch(''+config.endPointBaseURL+'/dataservices/listObjects?listall=bidang')
  .then(function(response){
    return response.json();
  })
  .then(function(data){
    if (data.status == 201) {
      const bidangSelector = document.getElementById('bidang');
      let datarow = data.dataarray;
      datarow.forEach(function(value){
        let option = document.createElement('option');
        option.text = value.bidang;
        option.value = value.id+'|'+value.bidang+'|'+value.module;
        if(value.id == selectedid){
          option.selected = true;
        }
        bidangSelector.appendChild(option);
      });
    } else {
      
    }
  })
  .catch(function(err){

  });
};

administrator._populateSubBidangSelector = function(bidangid){
  if(bidangid == '0|_UNDEFINED_|_UNDEFINED_' || bidangid == '0'){
    document.getElementById('subbidang').disabled = true;
  } else {
    fetch(''+config.endPointBaseURL+'/dataservices/listObjects?listall=ddsubbidang&bidangid='+bidangid+'')
    .then(function(response){
      return response.json();
    })
    .then(function(data){
      if (data.status == 201) {
        const subBidangSelector = document.getElementById('subbidang');
        let firstOption = document.createElement('option');
        firstOption.text = 'Pilih Sub Bagian/Bidang';
        firstOption.value = '0|_UNDEFINED_|_UNDEFINED_';
        subBidangSelector.appendChild(firstOption);
        let datarow = data.dataarray;
        datarow.forEach(function(value){
          let option = document.createElement('option');
          option.text = value.subbidang;
          option.value = value.id+'|'+value.subbidang+'|'+value.module;
          subBidangSelector.appendChild(option);
        });
      } else {
        document.getElementById('subbidang').disabled = true;
        document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Error: Pilihan bidang tidak dapat bekerja sebagaimana mestinya. Hubungi Administrator Sistem Anda.</div>";
        setTimeout(function(){
          document.getElementById('appFormNotice').innerHTML = '';
        },2000);
      }
    })
    .catch(function(err){
      document.getElementById('subbidang').disabled = true;
      document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Error: "+ err +".</div>";
      setTimeout(function(){
        document.getElementById('appFormNotice').innerHTML = '';
      },2000);
    });
  }
};

