administrator._usersOperation = function() {
  let itemstoreset = document.querySelectorAll('.item-to-resetpassword'), 
    itemtoedit = document.querySelectorAll('.item-to-edit'), 
    itemtodeactivate = document.querySelectorAll('.item-to-deactivate'),
    itemtoactivate = document.querySelectorAll('.item-to-activate');
  for (let i = 0; i < itemstoreset.length; i++) {
    let self = itemstoreset[i];
    self.addEventListener('click', function (event) {
      event.preventDefault();
      let userid = this.id;
      let confirmresetpassword = confirm('Benar-benar akan me-reset password user ini?');
      if(confirmresetpassword == true){
        fetch(''+config.endPointBaseURL+'/dataservices/resetPassword', {
          method: 'POST',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({"userid":userid})
        })
        .then(function(response){
          return response.json();
        })
        .then(function(data){
          alert(data.message);
        })
        .catch(function(err){
          console.log(err);
        });
      } else {
        return false;
      }
    }, false);
  }
  for (let i = 0; i < itemtoedit.length; i++) {
    let self = itemtoedit[i];
    self.addEventListener('click', function (event) {  
      event.preventDefault();
      let _arrUserData = this.id.split('@@');
      let _arrUserModule = _arrUserData[14].toString().split('-');
      if(_arrUserData[18] == '1'){
        document.getElementById('app_body').innerHTML = _templates._createFormEditUser();
        document.getElementById('idx').value = _arrUserData[0];
        document.getElementById('verificator').value = _arrUserData[16];
        document.getElementById('collector').value = _arrUserData[17];
        document.getElementById('orealname').value = _arrUserData[10];
        document.getElementById('ousername').value = _arrUserData[12];
        document.getElementById('oinisial').value = _arrUserData[11];
        document.getElementById('realname').value = _arrUserData[10];
        document.getElementById('username').value = _arrUserData[12];
        document.getElementById('inisial').value = _arrUserData[11];
        if(_arrUserData[16] == '1'){
          document.getElementById('is_verificator').checked = true;
        }
        if(_arrUserData[17] == '1'){
          document.getElementById('is_collector').checked = true;
        }
        administrator._activateEditSupervisorUserForm();
      } else {
        document.getElementById('app_body').innerHTML = _templates._createFormEditStaffUser();
        document.getElementById('idx').value = _arrUserData[0];
        document.getElementById('origin').value = _arrUserData[1];
        document.getElementById('opdcode').value = _arrUserData[2];
        document.getElementById('context').value = _arrUserData[9];
        document.getElementById('spvidx').value = _arrUserData[8];
        document.getElementById('verificator').value = _arrUserData[16];
        document.getElementById('collector').value = _arrUserData[17];
        document.getElementById('spvstatus').value = _arrUserData[18];
        document.getElementById('orealname').value = _arrUserData[10];
        document.getElementById('ousername').value = _arrUserData[12];
        document.getElementById('oinisial').value = _arrUserData[11];
        document.getElementById('realname').value = _arrUserData[10];
        document.getElementById('username').value = _arrUserData[12];
        document.getElementById('inisial').value = _arrUserData[11];
        if(_arrUserData[16] == '1'){
          document.getElementById('is_verificator').checked = true;
        }
        if(_arrUserData[17] == '1'){
          document.getElementById('is_collector').checked = true;
        }
        administrator._populateBidangSelectorWithSelected(_arrUserData[3]);
        if(_arrUserModule[1] == 'staf'){
          administrator._populateSubBidangSelector(_arrUserData[3]);
        } else {
          if(_arrUserModule[1] == 'kabid'){
            administrator._populateSubBidangSelector(_arrUserData[3]);
          } else {
            administrator._populateSubBidangSelectorWithSelected(_arrUserData[3],_arrUserData[5]);
          }
        }
        administrator._activateEditStaffUserForm();
      }
      document.getElementById('realname').focus();
    }, false);
  }
  for (let i = 0; i < itemtodeactivate.length; i++) {
    let self = itemtodeactivate[i];
    self.addEventListener('click', function (event) {  
      event.preventDefault();
      let userid = this.id;
      let confirmdeactivateuser = confirm('Benar-benar akan men-deaktivasi user ini?');
      if(confirmdeactivateuser == true){
        fetch(''+config.endPointBaseURL+'/dataservices/deactivateUser', {
          method: 'POST',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({"userid":userid})
        })
        .then(function(response){
          return response.json();
        })
        .then(function(data){
          alert(data.message);
          administrator._createModuleUsersManagement();
        })
        .catch(function(err){
          console.log(err);
        });
      } else {
        return false;
      }
    }, false);
  }
  for (let i = 0; i < itemtoactivate.length; i++) {
    let self = itemtoactivate[i];
    self.addEventListener('click', function (event) {  
      event.preventDefault();
      let userid = this.id;
      let confirmactivateuser = confirm('Benar-benar akan mengaktivasi user ini?');
      if(confirmactivateuser == true){
        fetch(''+config.endPointBaseURL+'/dataservices/activateUser', {
          method: 'POST',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({"userid":userid})
        })
        .then(function(response){
          return response.json();
        })
        .then(function(data){
          alert(data.message);
          administrator._createModuleUsersManagement();
        })
        .catch(function(err){
          console.log(err);
        });
      } else {
        return false;
      }
    }, false);
  }
};

