import config from '../config';
import helpers from '../helpers';
import _templates from './_component-templates';
import _helpers from './_layout-helpers';

var menusetting = {};

//var $ = jQuery;

menusetting._createSettingLayout = function() {
  document.getElementById('app').innerHTML = _templates._baseSidebarLayoutTemplate();
  document.getElementById('sidebar').innerHTML = _templates._settingSidebarMenu();
  menusetting._activateSidebarMenu();
};

menusetting._activateSidebarMenu = function() {
  let elements = document.querySelectorAll('.list-group-item');
  for (let i = 0; i < elements.length; i++) {
    let self = elements[i];
    self.addEventListener('click', function (event) {  
      event.preventDefault();
      if (document.querySelector('.list-group-item.list-group-item-action.active') !== null) {
        document.querySelector('.list-group-item.list-group-item-action.active').classList.remove('active');
      }
      switch(this.id){
        case 'changepassword':
            menusetting._createModuleChangePassword();
          break;
        default:
          break;
      }
      this.classList.toggle('active');
    }, false);
  }
};

menusetting._createModuleChangePassword = function() {
  let _userid = sessionStorage.getItem('userid'), _oldpassword = sessionStorage.getItem('hashedpassword');
  let dom = _templates._changePassword();
  document.getElementById('app_content').innerHTML = dom;
  document.getElementById('userid').value = _userid;
  document.getElementById('oldpassword').value = _oldpassword;
  document.getElementById('newpassword').focus();
  menusetting._activateChangePasswordForm();
};

menusetting._activateChangePasswordForm = function() {
  let appFormReset = document.getElementById('appFormReset');
  let appForm = document.getElementById('appForm');
  appFormReset.addEventListener('click', function(evt){
    evt.preventDefault();
    document.getElementById('newpassword').value = '';
    document.getElementById('cnfpassword').value = '';
    document.getElementById('newpassword').focus();
    return false;
  });
  appForm.addEventListener('submit', function(evt){
    evt.preventDefault();
    let opw = document.getElementById('oldpassword').value, npw = document.getElementById('newpassword').value, cpw = document.getElementById('cnfpassword').value;
    if(npw.length < 4 || cpw.length < 4 || npw != cpw){
      document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-1 pr-1 text-left' role='alert'>Password baru Anda tidak memenuhi syarat.</div>";
      setTimeout(function(){
        document.getElementById('newpassword').value = '';
        document.getElementById('cnfpassword').value = '';
        document.getElementById('newpassword').focus();
        document.getElementById('appFormNotice').innerHTML = '';
      },3000);
    } else {
      let form = document.querySelector('#appForm'),
        formData = helpers.serialize(form), 
        jsonData = helpers.QueryStringToJSON(decodeURIComponent(formData));
      fetch(''+config.endPointBaseURL+'/dataservices/changePassword', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(jsonData)
      })
      .then(function(response){
        return response.json();
      })
      .then(function(data){
        if (data.status == '201') {
          document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-success pl-1 pr-1 text-left' role='alert'>"+ data.message +"</div>";
          setTimeout(function(){
            helpers._signout();
          },1000);
        } else {
          document.getElementById('appFormNotice').innerHTML = "<div class='alert alert-danger pl-1 pr-1 text-left' role='alert'>"+ data.message +"</div>";
          setTimeout(function(){
            document.getElementById('newpassword').value = '';
            document.getElementById('cnfpassword').value = '';
            document.getElementById('newpassword').focus();
            document.getElementById('appFormNotice').innerHTML = '';
          },2000);
        }
      })
      .catch(function(err){
        console.log(err);
      });
    }
    return false;
  });
};

export default menusetting;