import config from '../config';
import helpers from '../helpers';
import _templates from './_component-templates';
import _helpers from './_layout-helpers';
// import {DataTable} from '../../node_modules/simple-datatables/dist/index';

var dashboard = {};

dashboard._createDashboardLayout = function() {
  let activeModule = sessionStorage.getItem('module');
  if(activeModule){
    if(activeModule == 'development'){
      document.getElementById('app').innerHTML = _templates._baseDashboardLayoutTemplate();
      dashboard._createBPHTBProgressChart();
    } else {
      document.getElementById('app').innerHTML = _templates._baseDashboardLayoutTemplate();
      dashboard._createBPHTBProgressChart();
    }
  } else {
    
  }
};

dashboard._createBPHTBProgressChart = function(){
  let dt = new Date();
  fetch(''+config.endPointBaseURL+'/dataservices/getBPHTBChartSpec?th_pajak='+ dt.getFullYear() +'')
  .then(function(response){
    return response.json();
  })
  .then(function(data){
    helpers._renderChart(data,'canvas','chart-container');
  })
  .catch(function(err){
    document.getElementById('chart-container').innerHTML = "<div class='alert alert-danger pl-0 pr-0 text-center' role='alert'>Error: "+ err +".</div>";
  });
};

export default dashboard;